<script>
export default {
  name: "App",
  data(){
    return {
      ticker: 'Выгода',
      placeholder: "что-нибудь введите уж",
      graph: [],
      tickers:[
        {name:'Цена покупки', price: 3699401.4},
      ],
      boughtBTC: 0.005671,
      sel:null,
      api_key:'7e75c026515a4493141e10b8adeddef43a8fc7f7b4e23235d5b7b4da09ee968d',
      mainURL:'https://min-api.cryptocompare.com'
    }
  },
  methods: {
    addTicker(){
      const actualTicker = {
        name:this.ticker,
        price: 'n/a'
      }
      this.tickers.push(actualTicker)
      setInterval(async () =>{
        const f = await fetch(`${this.mainURL}/data/price?fsym=BTC&tsyms=USD,RUB,EUR&api_key=${this.api_key}`)
        const data = await f.json();
            let x = (data.RUB - this.tickers[0].price)*this.boughtBTC
        this.tickers.find(ticker => ticker.name === actualTicker.name).price = x.toFixed(1)
      },3000)
      this.ticker=""
    },

    select(ticker){
      this.sel = ticker;
      this.graph = []
    },

    removeTicker(toRemove){
      this.tickers = this.tickers.filter(t=> t !== toRemove)
      this.sel = null
    },

  }
};
</script>

