@input.prevent
@click.stop
 - это команды-сокращения от event.preventDefault и stopPropagation
 например, чтобы событие не пошлО дальше (скажем, при клике на input не вызвалась команда для клика по вышележащему div)

v-cloak - делает рендеринг страницы менее рваной (т.е. не показывает её, пока не отрендерится) - зависит от условия
  ставится атрибут в конце открывающего тега, без параметров. СНачала надо прописать условие в стилях
  <style> [v-cloak]{display: none} </style>

v-text="variable" заменяет интерполяцию {{между тегами}} на "внутри тега"
v-pre выведет то, что между тегами, без компиляции (как есть)
v-html="variable" внутри тега выведет переменную variable как HTML, если она задумана как HTML (<h1>Bla-bla</h1>
<tag v-once> {{ variable }} </tag> - покажет переменную и оставит её в том виде, даже если потом её кто-то поменяет
v-on: это "директива" для работы с событиями (шортхэнд @, т.е. @input вместо v-on:input)
{{ }} это интерполяция, для вывода в область текста. Сюда можно вставлять переменную или работу  с ними,
v-bind: - это чтобы работать с переменными(динамическими значениями) в атрибутах тегов (шортхэнд :)
v-on:keypress.enter="addNewNote" keypress.enter - это модификатор
v-for - это работа с циклом в массиве. Например, показать повторяющуюся HTML-структуру
  также v-for="value in {Object}" выведет все значения (но без ключей). ЧТобы показать ключи, надо v-for="(value, key) in {Object}"
  для вывода, например, Див-ов из массива, надо кроме v-for="elem in Array" ещё привязаться v-bind:key="elem"
  (краткая форма :key="elem")
        v-bind:key="уникальное значение элемента" \\для работы с цикло-массивами, чтобы не было багов
  Также for in может быть for i in 7, например

v-if="notes.length === 0" директива для условия. Можно сопровождать v-else у сиблинга и v-else-if
убирает или создаёт элемент в зависимости от условия
v-show="условие" похоже на v-if, но только элемент скрывается, но не удаляется из структуры

$event - это event в Vue (браузерное событие)
раздел computed - нужен для оптимизации вычислений, туда например мы засунули метод, сообщающий удвоенную длину массива,
при том, что в не-computed разделе он вызывался после любого изменения в строке набора текста (ОПТИМИЗАЦИЯ!!!)
