--computed-свойства - это Геттеры и Сеттеры, поэтому их мы вызываем без ().
    Например, в computed мы пишем метод divide(), который возвращает изменённый массив,
    и далее мы можем крутить v-for="elem in divide", используя divide как переменную
--watch - наблюдение за изменениями. Например, чтобы следить за изменениями переменной inputValue, надо создать одноимённый
метод inputValue(){}

v-model - отвечает за двухстороннее связывание (синхронизация между UI и данными)

